.chart_container {
  width: 100%;
  height: 400px;
  margin-bottom: 60px;

  .chart_tooltip {
    background-color: #fff;
    padding: 10px 10px 5px;
    box-shadow: 0 0 3px #C4C4C4;
    border-radius: 4px;
    display: flex;
    flex-direction: column;

    div {
      margin-bottom: 5px;
      font-size: 12px;
      color: #818181;
    }
  }

  .chart_actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}